import React from 'react';
import PropTypes from 'prop-types';
import { uppercaseFirstLetterLowercaseRest } from '../../util';
import type { ResultsAccommodation, ResultsAccommodationMetaDataList } from '../../types/cards/card';

/**
 * Render a Funnelback result as an accommodation card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */

function Finder__Results__Accommodation(props: ResultsAccommodation) {
    const mph = 60,
        calculateTime = (minutes: number): string | number =>
            Math.floor(minutes / mph) > 0
                ? Math.floor(minutes / mph) + ' hour ' + (minutes % mph)
                : minutes,
        thumbnail = props.details && props.details.listMetadata && props.details.listMetadata.thumbnail && props.details.listMetadata.thumbnail[0] && (
            <img
                src={props.details.listMetadata.thumbnail[0]}
                alt=""
                className="card__thumbnail"
            />
        ),
        level = props.details && props.details.listMetadata && props.details.listMetadata.level && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-graduation-cap icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">Level:</span>{' '}
                <span>
                    {uppercaseFirstLetterLowercaseRest(
                        props.details.listMetadata.level.join('|')
                    )
                        .split('|')
                        .join(' or ')}
                </span>
            </p>
        ),
        features = props.details.listMetadata
            ? Object.keys(props.details.listMetadata)
                .filter((meta) => {
                    const key = meta as keyof ResultsAccommodationMetaDataList;
                    const value = props.details.listMetadata![key];
                    return ['accessible', 'catered', 'ensuite'].includes(meta) && value && value[0] === 'Yes';
                })
                .map((meta) => meta.replace(/ensuite/, 'en suite'))
                .sort()
                .join(', ')
            : '',
        type = props.details && props.details.listMetadata && props.details.listMetadata.type && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-sharp fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">Type:</span>{' '}
                <span>
                    {props.details.listMetadata.type.length > 2
                        ? uppercaseFirstLetterLowercaseRest(
                              props.details.listMetadata.type.join('|')
                          )
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ' or ' +
                          uppercaseFirstLetterLowercaseRest(
                              props.details.listMetadata.type.join('|')
                          )
                              .split('|')
                              .slice(-1)
                        : uppercaseFirstLetterLowercaseRest(
                              props.details.listMetadata.type.join('|')
                          )
                              .split('|')
                              .join(' or ')}
                    {features && ' (' + features + ')'}
                </span>
            </p>
        ),
        price = props.details.listMetadata && props.details.listMetadata.price && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-pound-sign icon"
                    aria-hidden="true"
                />{' '}
                <span className="sr-only">Price:</span>{' '}
                <span>
                    {props.details.listMetadata.price[0]} (
                    {props.details.listMetadata.deposit && props.details.listMetadata.deposit[0]} deposit)
                </span>
            </p>
        ),
        transport = props.details.listMetadata && props.details.listMetadata.transport && (
            <React.Fragment>
                {' '}
                ({calculateTime(parseInt(props.details.listMetadata.transport[0]))}{' '}
                minutes by public transport)
            </React.Fragment>
        ),
        location =
            props.details.listMetadata &&
            props.details.listMetadata.location &&
            props.details.listMetadata.location[0] ? (
                <React.Fragment>
                    <span className="sr-only">Distance to</span>{' '}
                    <div
                        className={`card__details__location card__details__location-${props.details.listMetadata.location[0]
                            .trim()
                            .replace(' ', '-')
                            .toLowerCase()}`}
                    >
                        <span
                            className="fas fa-fw fa-location-dot icon"
                            aria-hidden="true"
                        />{' '}
                        <span>{props.details.listMetadata.location[0]}</span>
                        <span className="sr-only"> campus</span>
                    </div>
                </React.Fragment>
            ) : (
                <span className="sr-only">
                    Distance to Northampton Square campus:
                </span>
            ),
        distance = props.details.listMetadata && props.details.listMetadata.walk && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-walking icon"
                    aria-hidden="true"
                />{' '}
                {location}
                <span>
                    {calculateTime(parseInt(props.details.listMetadata.walk[0]))} minutes
                    walk to campus
                    {transport}
                </span>
            </p>
        );

    return (
        <li className="card card--accommodation">
            <a
                href={props.details.liveUrl}
                className="card__anchor card__details"
            >
                {thumbnail}
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    {distance}
                    {type}
                    {price}
                    {level}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Accommodation.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Accommodation;
