import React from 'react';
import PropTypes from 'prop-types';
import { formatTime, formatReactDate } from '../../util';
import type { ResultsEvents } from '../../types/cards/card';

function formatShortDate(dateString: string) {
    const months = [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
        ],
        date = new Date(dateString),
        month = months[date.getUTCMonth()];

    return (
        <p className="card--event__label--shortdate">
            <span>{date.getUTCDate()}</span> {month}
        </p>
    );
}

function compareDates(startDate: string, endDate: string, showTime:number) {
    const sDate = new Date(startDate),
        eDate = new Date(endDate),
        noTimeSDate = new Date(sDate.setUTCHours(0, 0, 0)),
        noTimeEDate = new Date(eDate.setUTCHours(0, 0, 0)),
        time = showTime ? (
            <React.Fragment>
                , {formatTime(startDate)} - {formatTime(endDate, true)}
            </React.Fragment>
        ) : null;

    return noTimeSDate.getTime() === noTimeEDate.getTime() ? (
        <p className="card__type true">
            {formatReactDate(sDate)}
            {time}
        </p>
    ) : (
        <p className="card__type false">
            {formatReactDate(sDate)} - {formatReactDate(eDate)}
            {time}
        </p>
    );
}

/**
 * Render a Funnelback result as an event card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Event(props: ResultsEvents) {
    const shortDate =
            props.details.listMetadata &&
            props.details.listMetadata.d &&
            formatShortDate(props.details.listMetadata.d[0]),
        location = props.details.listMetadata && props.details.listMetadata.location && (
            <p className="card__tag">
                <span
                    className="fas fa-fw icon fa-map-marker-alt"
                    aria-hidden="true"
                ></span>{' '}
                <span>Location:</span>{' '}
                <span>{props.details.listMetadata.location.join(', ')}</span>
            </p>
        ),
        audience = props.details.listMetadata && props.details.listMetadata.audience && (
            <p className="card__tag">
                <span
                    className="fas fa-fw icon fa-users"
                    aria-hidden="true"
                ></span>{' '}
                <span>Audience:</span>{' '}
                <span>
                    {Array.from(new Set(props.details.listMetadata.audience))
                        .map((str) => {
                            let capitalized = `${str[0].toUpperCase()}${str.slice(
                                1
                            )}`;
                            return capitalized.replace(/-/g, ' ');
                        })
                        .join('|')
                        .split('|')
                        .join('; ')}
                </span>
            </p>
        ),
        thumbnail = props.details.listMetadata && props.details.listMetadata.thumbnail && (
            <img
                src={props.details.listMetadata.thumbnail[0]}
                alt=""
                className="card__thumbnail"
            />
        ),
        eventStartDate =
            props.details.listMetadata && props.details.listMetadata.d && props.details.listMetadata.d[0],
        eventEndDate =
            props.details.listMetadata && props.details.listMetadata.d && props.details.listMetadata.d[1],
        eventLabel = eventStartDate && eventEndDate && props.details.listMetadata && props.details.listMetadata.displayTime && compareDates(
            eventStartDate,
            eventEndDate,
            parseInt(props.details.listMetadata.displayTime[0])
        );

    return (
        <li className="card card--event card--landscape">
            <a
                href={props.details.liveUrl}
                className="card__anchor card__details"
            >
                {thumbnail}
                {shortDate}
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    <p className="card__description">
                        {props.details.listMetadata && props.details.listMetadata.c &&
                            props.details.listMetadata.c[0]}
                    </p>
                    {eventLabel}
                    {location}
                    {audience}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Event.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Event;
