import { flattenObj } from '../../shared/util';
import type { FacetDetails } from '../types/facet';
import type { QueryDetails } from '../types/query'

function remove(obj: Record<string, any>, key: string): void {
    for (let k in obj) {
        if (k === key) {
            delete obj[k];
        } else if (typeof obj[k] === 'object' && obj[k] !== null) {
            remove(obj[k], key);
        }
    }
}

export function hyperLink(query: QueryDetails, facet: FacetDetails | null, pageNumber?: number, numberRank?: number) {
    const newStartRank = pageNumber && numberRank && 1 + (pageNumber - 1) * numberRank;
    let newQuery = JSON.parse(JSON.stringify(query)); //deep clone

    facet && facet.type === 'tag' && facet.meta && remove(newQuery, facet.meta);

    const flattenQuery = flattenObj(newQuery);

    return (
        '?' +
        Object.entries(flattenQuery)
            .filter((key) => {
                let param = Object.values(key);

                if (param[1] === '') {
                    return false;
                } else {
                    switch (param[0]) {
                        case 'interacted':
                            return false;
                        case 'num_rank':
                            return false;
                        case 'start_rank':
                            return false;
                        default:
                            return true;
                    }
                }
            })
            .map((key) => {
                let param = Object.values(key);

                if (/fixedParameters/.test(param[0])) {
                    return (
                        encodeURIComponent(param[0].substring(16)) +
                        '=' +
                        encodeURIComponent(param[1])
                    );
                } else if (/fixedFacets/.test(param[0])) {
                    return (
                        encodeURIComponent(
                            `meta_${param[0].substring(12)}_and`
                        ) +
                        '=' +
                        encodeURIComponent(param[1])
                    );
                } else if (/parameters/.test(param[0])) {
                    return (
                        encodeURIComponent(param[0].substring(11)) +
                        '=' +
                        encodeURIComponent(param[1])
                    );
                }
                switch (param[0]) {
                    case 'collection':
                        return (
                            encodeURIComponent(param[0]) +
                            '=' +
                            encodeURIComponent(param[1])
                        );
                    case 'query':
                        return (
                            encodeURIComponent(param[0]) +
                            '=' +
                            encodeURIComponent(param[1])
                        );
                    case 'sortType':
                        return (
                            encodeURIComponent('sort') +
                            '=' +
                            encodeURIComponent(param[1])
                        );
                    case 'startRank':
                        return (
                            encodeURIComponent('start_rank') +
                            '=' +
                            encodeURIComponent(facet ? param[1] : newStartRank)
                        );
                    case 'numRanks':
                        return (
                            encodeURIComponent('num_rank') +
                            '=' +
                            encodeURIComponent(param[1])
                        );
                    default:
                        return (
                            encodeURIComponent(`meta_${param[0]}_and`) +
                            '=' +
                            encodeURIComponent(param[1])
                        );
                }
            })
            .join('&')
    );
}
