import PropTypes from 'prop-types';
import React from 'react';
import Sort from '../filters/finder__sort';
import type { SummaryResults } from '../../types/results/results';

/**
 * Results heading, summarising the response.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Summary(props: SummaryResults) {
    const result =
            props.totalMatching === 1
                ? props.summariseAs.singular
                : props.summariseAs.plural,
        formatter = new Intl.NumberFormat('en-GB'),
        sort =
            props.config.sort.length > 1 && props.config.displaySort ? (
                <div className="wrapper--finder__select--sort--desktop">
                    <Sort
                        config={props.config}
                        query={props.query}
                        update={props.update}
                    />
                </div>
            ) : null;

    if (props.totalMatching === 0) {
        return (
            <div className="finder__results__summary finder__results__summary--noresults" ref={props.summaryHeadingRef}>
                <h2 className="finder__results__summary__heading">
                    Your search did not match any {props.summariseAs.plural}.
                </h2>
                <p>Suggestions:</p>
                <ul>
                    <li>Make sure that all words are spelled correctly</li>
                    <li>Try different keywords</li>
                    <li>Try more general keywords</li>
                    <li>Try fewer keywords</li>
                    <li>Try fewer filters</li>
                    {Object.keys(props.query.facets).length > 0 && (
                        <li>
                            <button
                                type="button"
                                onClick={() => {
                                    props.clear(true);
                                }}
                            >
                                Reset filters
                            </button>
                        </li>
                    )}
                    {props.query.query && (
                        <li>
                            <button
                                type="button"
                                onClick={() => {
                                    const newQuery = props.query;

                                    newQuery.query = '';
                                    newQuery.sortType =
                                        props.config.sort[0].type;
                                    newQuery.misspelling = null;
                                    newQuery.startRank = 1;
                                    newQuery.interacted = true;

                                    props.update.query(newQuery);
                                    props.update.results(
                                        !props.update.updateState
                                    );
                                }}
                            >
                                Reset query
                            </button>
                        </li>
                    )}
                </ul>
            </div>
        );
    } else {
        return (
            <div className="finder__results__summary" ref={props.summaryHeadingRef}>
                <h2
                    aria-live="polite"
                    className="finder__results__summary__heading"
                >
                    {props.query.query ||
                    Object.keys(props.query.facets).length > 0
                        ? 'Matching'
                        : 'All'}{' '}
                    {result}{' '}
                    <span>
                        (showing{' '}
                        {props.totalMatching > props.numRanks && (
                            <>
                                <span>{formatter.format(props.currStart)}</span>
                                –<span>{props.currEnd && formatter.format(props.currEnd)}</span>{' '}
                                of{' '}
                            </>
                        )}
                        <span>{formatter.format(props.totalMatching)}</span>{' '}
                        {result}
                        {props.query.query && ` for “${props.query.query}”`})
                    </span>
                </h2>
                {sort}
            </div>
        );
    }
}

Finder__Results__Summary.propTypes = {
    clear: PropTypes.func,
    config: PropTypes.object,
    currEnd: PropTypes.number,
    currStart: PropTypes.number,
    numRanks: PropTypes.number,
    query: PropTypes.object,
    summariseAs: PropTypes.object,
    totalMatching: PropTypes.number,
    update: PropTypes.object,
};

export default Finder__Results__Summary;
