import React from 'react';
import PropTypes from 'prop-types';
import type { ParamCheckBox } from '../../types/filters/filters';

function Finder__paramCheckbox(props: ParamCheckBox) {
    const stringLength = 16,
        stringOffset = -4,
        randomNumber = Math.random().toString(stringLength).slice(stringOffset),
        pastEventsResponseTotal =
            props.pastEventsResponse &&
            props.pastEventsResponse.resultPacket.resultsSummary &&
            props.pastEventsResponse.resultPacket.resultsSummary.fullyMatching,
        pastEventsPartialResponseTotal =
            props.pastEventsResponse &&
            props.pastEventsResponse.resultPacket.resultsSummary &&
            props.pastEventsResponse.resultPacket.resultsSummary
                .partiallyMatching,
        toggleChecked = props.query.parameters && props.facet.meta in props.query.parameters,
        totalText = pastEventsResponseTotal
            ? pastEventsResponseTotal
            : pastEventsPartialResponseTotal
            ? pastEventsPartialResponseTotal
            : 0;

    const toggleFacet = () => {
        let newQuery = props.query;

        if(!newQuery.parameters){
            newQuery.parameters = {};
        }

        
        if(toggleChecked){
            delete newQuery.parameters[props.facet.meta]
        }else if(props.facet.checkedValue !== undefined){
            (newQuery.parameters[props.facet.meta] =
                  props.facet.checkedValue);
        }

        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    if (totalText > 0) {
        return (
            <div className="finder__filter finder__checkbox">
                <input
                    type="checkbox"
                    id={`${props.facet.funnelbackName}--${randomNumber}`}
                    name={props.facet.meta}
                    value={(props.facet.checkedValue ?? '') as string}
                    onChange={() => toggleFacet()}
                    checked={toggleChecked}
                />
                <div
                    className="finder__checkbox__indicator finder__checkbox__indicator"
                    aria-hidden="true"
                    onClick={() => toggleFacet()}
                >
                    {toggleChecked ? (
                        <span className="fa fa-fw fas fa-check icon"></span>
                    ) : null}
                </div>
                <label
                    className="finder__filters__label--always"
                    htmlFor={`${props.facet.funnelbackName}--${randomNumber}`}
                >
                    {props.facet.name}
                    {!toggleChecked && totalText && ' (' + totalText + ')'}
                </label>
            </div>
        );
    } else {
        return null;
    }
}

Finder__paramCheckbox.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    pastEventsResponse: PropTypes.object,
    update: PropTypes.object,
    dependencies: PropTypes.arrayOf(PropTypes.object),
};

export default Finder__paramCheckbox;
