import React from 'react';
import PropTypes from 'prop-types';
import type { ResultsGeneric } from '../../types/cards/card';
/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Generic(props: ResultsGeneric) {
    const school = props.details.listMetadata && props.details.listMetadata.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-sharp fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>
                    {props.details.listMetadata.school.length > 2
                        ? props.details.listMetadata.school
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.listMetadata.school.slice(-1)
                        : props.details.listMetadata.school.join(', and ')}
                </span>
            </p>
        ),
        department = props.details.listMetadata && props.details.listMetadata.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-sharp fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span>{' '}
                <span>
                    {props.details.listMetadata.department.length > 2
                        ? props.details.listMetadata.department
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.listMetadata.department.slice(-1)
                        : props.details.listMetadata.department.join(', and ')}
                </span>
            </p>
        ),
        title = props.details.title && (
            <h3 className="card__heading">
                {props.details.title.indexOf('School') >= 0
                    ? props.details.title.replace('&amp;', '&')
                    : props.details.title}
            </h3>
        );

    return (
        <li className="card card--generic">
            <a
                href={props.details.liveUrl}
                className="card__anchor card__details"
            >
                <div className="card__details__text">
                    {title}
                    <p className="card__description">
                        {props.details.listMetadata && props.details.listMetadata.c &&
                            props.details.listMetadata.c[0]}
                    </p>
                    {school}
                    {department}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Generic.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Generic;
