import React from 'react';
import PropTypes from 'prop-types';
import type { FixedParameters } from '../../types/query';
function Finder__hiddenInput(props: FixedParameters) {
    return (
        <input
            type="hidden"
            id={props.name}
            name={props.name}
            value={props.value}
        />
    );
}

Finder__hiddenInput.propTypes = {
    name: PropTypes.string,
    value: PropTypes.string,
};

export default Finder__hiddenInput;
