import React from 'react';
import PropTypes from 'prop-types';
import type { Reset } from '../../types/filters/filters';

/**
 * Clear input button.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Reset(props: Reset) {
    if (props.matrixState) {
        return (
            <a href="?" className="finder__reset" type="button">
                <span className="fa-sharp fa-solid fa-xmark icon" />
                <span className="finder__reset__text">Reset</span>
            </a>
        );
    } else {
        return (
            <button
                className="finder__reset"
                type="button"
                onClick={() => {
                    props.clear(props.resetSort);
                }}
            >
                <span className="fa-sharp fa-solid fa-xmark icon" />
                <span className="finder__reset__text">Reset</span>
            </button>
        );
    }
}

Finder__Reset.propTypes = {
    clear: PropTypes.func,
    resetSort: PropTypes.bool,
    matrixState: PropTypes.bool,
};

export default Finder__Reset;
