import React from 'react';
import PropTypes from 'prop-types';
import { hyperLink } from '../../logic/hyper-link';
import useHasMounted from '../../logic/has-mounted';
import type { Tag } from '../../types/filters/filters';

function Finder__Tag(props: Tag) {
    const hasMounted = useHasMounted();

    if (!hasMounted) return null;

    // reduce the facet configuration to an array of all possible values for
    // the facet
    const allFacets = props.facet && props.facet.values&&  props.facet.values.reduce((acc, data) => {
        return [...acc, data];
    }, []);

    // reduce the Funnelback response for the facet to an array of valid
    // values for the current query
    const responseFacets =
        props.responseFacet[0] && props.responseFacet[0].allValues
            ? props.responseFacet[0].allValues.reduce((acc, data) => {
                  return [...acc, data.data];
              }, [])
            : [];

    // count how many possible facets are not valid for the current query
    // and will be hidden
    const hiddenFacets = allFacets && allFacets
        .map((facet) => facet.data)
        .filter(
            (facet) => responseFacets.indexOf(facet.toLowerCase()) < 0
        ).length;

    const deleteFacet = () => {
        const newQuery = props.query;

        props.dependencies && props.dependencies.map((facet) => {
            delete newQuery.facets[facet.meta];
        });

        delete newQuery.facets[props.facet.meta];

        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const labelText = props.query.facets[props.facet.meta] && props.facet.values && hiddenFacets &&
        props.facet.values.length >= hiddenFacets && (
            <React.Fragment>
                <span
                    className="fa-sharp fa-solid fa-xmark icon"
                    aria-hidden="true"
                ></span>
                <span className="finder__tag__text">
                    <span className="sr-only">Remove filter for </span>
                    {props.facet.name}:{' '}
                    {
                        props.facet.values.filter(
                            (value) =>
                                value.data.toLowerCase() ===
                                props.query.facets[
                                    props.facet.meta
                                ].toLowerCase()
                        )[0].label
                    }
                </span>
            </React.Fragment>
        );

    const ssrHiddenField =
        props.matrixState && props.query.facets[props.facet.meta] ? (
            <input
                type="hidden"
                name={`meta_${props.facet.meta}_and`}
                value={props.query.facets[props.facet.meta]}
            />
        ) : null;

    if (
        props.facet.values && hiddenFacets &&
        props.facet.values.length >= hiddenFacets &&
        props.query.facets[props.facet.meta]
    ) {
        return (
            <div className="finder__filter finder__tag">
                {ssrHiddenField}
                {props.matrixState && props.query.facets[props.facet.meta] ? (
                    <a href={hyperLink(props.query, props.facet)}>
                        {labelText}
                    </a>
                ) : (
                    <button onClick={() => deleteFacet()} type="button">
                        {labelText}
                    </button>
                )}
            </div>
        );
    } else {
        return null;
    }
}

Finder__Tag.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
    dependencies: PropTypes.arrayOf(PropTypes.object),
    matrixState: PropTypes.bool,
};

export default Finder__Tag;
