import React from 'react';
import PropTypes from 'prop-types';
import type { CheckBox } from '../../types/filters/filters';

function Finder__Checkbox(props: CheckBox) {
    const randomNumber = props.mobile ? 'mobile' : 'desktop',
        toggleChecked = props.facet.meta in props.query.facets,
        responseFacetValue =
            props.responseFacet[0] &&
            props.responseFacet[0].allValues &&
            props.responseFacet[0].allValues.filter(
                (value) =>
                    props.facet.checkedValue && value.data.toLowerCase() ===
                    props.facet.checkedValue.toLowerCase()
            );

    if (
        responseFacetValue &&
        responseFacetValue[0] &&
        responseFacetValue[0].count > 0
    ) {
        const toggleFacet = () => {
            const newQuery = props.query;

            props.dependencies && props.dependencies.map((facet) => {
                delete newQuery.facets[facet.meta];
            });

            toggleChecked
                ? delete newQuery.facets[props.facet.meta]
                : props.facet.checkedValue ?
                (newQuery.facets[props.facet.meta] =
                      props.facet.checkedValue)
                : null;

            newQuery.startRank = 1;
            newQuery.misspelling = null;
            newQuery.interacted = true;

            props.update.query(newQuery);
            props.update.results(!props.update.updateState);
        };

        return (
            <div className="finder__filter finder__checkbox">
                <input
                    type="checkbox"
                    id={`meta_${props.facet.meta}_and--${randomNumber}`}
                    name={`meta_${props.facet.meta}_and`}
                    value={props.facet.checkedValue}
                    onChange={() => toggleFacet()}
                    checked={toggleChecked}
                />
                <div
                    className="finder__checkbox__indicator finder__checkbox__indicator"
                    aria-hidden="true"
                    onClick={() => toggleFacet()}
                >
                    {toggleChecked ? (
                        <span className="fa fa-fw fas fa-check icon"></span>
                    ) : null}
                </div>
                <label
                    className="finder__filters__label--always"
                    htmlFor={`meta_${props.facet.meta}_and--${randomNumber}`}
                >
                    {props.facet.name}
                    {!toggleChecked &&
                        responseFacetValue &&
                        ' (' + responseFacetValue[0].count + ')'}
                </label>
            </div>
        );
    } else {
        return null;
    }
}

Finder__Checkbox.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
    dependencies: PropTypes.arrayOf(PropTypes.object),
    mobile: PropTypes.bool,
};

export default Finder__Checkbox;
