import PropTypes from 'prop-types';
import React from 'react';
import type { ResultsResearch } from '../../types/cards/card';
// import parse from 'html-react-parser';

/**
 * Render a Funnelback result as a PhD research project card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Research(props: ResultsResearch) {
    const school = props.details.listMetadata && props.details.listMetadata.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-sharp fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>
                    {props.details.listMetadata.school.length > 2
                        ? props.details.listMetadata.school
                              .slice(0, -1)
                              .join(', ') +
                          ' and ' +
                          props.details.listMetadata.school.slice(-1)
                        : props.details.listMetadata.school.join(' and ')}
                </span>
            </p>
        ),
        department = props.details.listMetadata && props.details.listMetadata.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-sharp fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span>{' '}
                <span>
                    {props.details.listMetadata.department.length > 2
                        ? props.details.listMetadata.department
                              .slice(0, -1)
                              .join(', ') +
                          ' and ' +
                          props.details.listMetadata.department.slice(-1)
                        : props.details.listMetadata.department.join(' and ')}
                </span>
            </p>
        ),
        centre = props.details.listMetadata&&  props.details.listMetadata.researchCentre && (
            <p className="card__tag">
                <span className="fas fa-fw fa-vial icon" aria-hidden="true" />{' '}
                {props.details.listMetadata.researchCentre.length > 1 ? (
                    <span>Research centres: </span>
                ) : (
                    <span>Research centre: </span>
                )}
                {/* <span>
                    {props.details.listMetadata.researchCentre.length > 2
                        ? parse(
                              props.details.listMetadata.researchCentre
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.listMetadata.researchCentre.slice(
                                      -1
                                  )
                          )
                        : parse(
                              props.details.listMetadata.researchCentre.join(
                                  ' and '
                              )
                          )}
                </span> */}
            </p>
        ),
        academic = props.details.listMetadata && props.details.listMetadata.academic && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-chalkboard-teacher icon"
                    aria-hidden="true"
                />{' '}
                {props.details.listMetadata.academic.length > 1 ? (
                    <span>Academics: </span>
                ) : (
                    <span>Academic: </span>
                )}
                {/* <span>
                    {props.details.listMetadata.academic.length > 2
                        ? parse(
                              props.details.listMetadata.academic
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.listMetadata.academic.slice(-1)
                          )
                        : parse(
                              props.details.listMetadata.academic.join(' and ')
                          )}
                </span> */}
            </p>
        ),
        student = props.details.listMetadata && props.details.listMetadata.student && (
            <p className="card__tag">
                <span className="fas fa-fw fa-users icon" aria-hidden="true" />{' '}
                {props.details.listMetadata.student.length > 1 ? (
                    <span>Students: </span>
                ) : (
                    <span>Student: </span>
                )}
                {/* <span>
                    {props.details.listMetadata.student.length > 2
                        ? parse(
                              props.details.listMetadata.student
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.listMetadata.student.slice(-1)
                          )
                        : parse(
                              props.details.listMetadata.student.join(' and ')
                          )}
                </span> */}
            </p>
        ),
        degree = props.details.listMetadata && props.details.listMetadata.relatedDegree && (
            <p className="card__tag">
                <span
                    className="far fa-fw fa-diploma icon"
                    aria-hidden="true"
                />{' '}
                {props.details.listMetadata.relatedDegree.length > 1 ? (
                    <span>Related degrees: </span>
                ) : (
                    <span>Related degree: </span>
                )}
                {/* <span>
                    {props.details.listMetadata.relatedDegree.length > 2
                        ? parse(
                              props.details.listMetadata.relatedDegree
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.listMetadata.relatedDegree.slice(
                                      -1
                                  )
                          )
                        : parse(
                              props.details.listMetadata.relatedDegree.join(
                                  ' and '
                              )
                          )}
                </span> */}
            </p>
        ),
        status = props.details.listMetadata && props.details.listMetadata.status && (
            <p className="card__tag">
                <span
                    className="far fa-fw fa-hourglass icon"
                    aria-hidden="true"
                />{' '}
                <span>Status:</span>{' '}
                <span>{props.details.listMetadata.status[0]} </span>
                {props.details.listMetadata.status[0] ===
                'Completed project' ? (
                    <span
                        className="far fa-fw fa-check-circle icon"
                        aria-hidden="true"
                    />
                ) : null}
            </p>
        ),
        site = props.details.listMetadata && props.details.listMetadata.site && (
            <p className="card__tag">
                <span className="far fa-fw fa-globe icon" aria-hidden="true" />{' '}
                <span>
                    <a href={props.details.listMetadata.site[0]}>
                        View case study site
                    </a>
                </span>
            </p>
        );

    return (
        <li className="card card--research">
            <div className="card__details">
                <div className="card__details__text">
                    <h3 className="card--research__title-link">
                        {props.details.title}
                    </h3>
                    <p className="card__description">
                        {props.details.listMetadata && props.details.listMetadata.c &&
                            props.details.listMetadata.c[0]}
                    </p>
                    {school}
                    {department}
                    {centre}
                    {degree}
                    {academic}
                    {student}
                    {status}
                    {site}
                </div>
            </div>
        </li>
    );
}

Finder__Results__Research.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Research;
