import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import type { CancelTokenSource } from 'axios';
import { suggest } from '../../logic/funnelback';
import Clear from './finder__clear';
import useHasMounted from '../../logic/has-mounted'; // ✅ added
import type { QueryInput } from '../../types/query/query';

const maximumSuggestions = 5;
const escapeKey = 'Escape',
    endKey = 'End',
    homeKey = 'Home',
    arrowUp = 'ArrowUp',
    arrowDown = 'ArrowDown';

function highlightQueryTerm(suggestion: string, partialQuery: string) {
    return (
        <span>
            <span className="sr-only">{suggestion}</span>
            <span aria-hidden="true">
                <span className="finder__suggestion">
                    {suggestion.slice(
                        suggestion.indexOf(partialQuery.toLowerCase()),
                        suggestion.indexOf(partialQuery.toLowerCase()) +
                            partialQuery.length
                    )}
                </span>
                {suggestion.slice(
                    suggestion.indexOf(partialQuery.toLowerCase()) +
                        partialQuery.length
                )}
            </span>
        </span>
    );
}

function Finder__Query(props: QueryInput) {
    const hasMounted = useHasMounted(); // ✅ detect client vs server

    const [partialQuery, setPartialQuery] = useState(props.query.query || '');
    const [suggestions, setSuggestions] = useState([]);
    const [call, setCall] = useState<CancelTokenSource | null>(null);
    const [inputId] = useState('finder--' + props.query.collection);
    const [showSuggestions, setShowSuggestions] = useState(false);
    const [activeSuggestionID, setActiveSuggestionID] = useState('');
    let textInput: HTMLInputElement | null = null;

    useEffect(() => {
        setPartialQuery(props.query.query);
    }, [props.query.query, props.updating]);

    useEffect(() => {
        return () => {
            if (call) call.cancel();
        };
    }, [call]);

    function focusInput() {
        textInput && textInput.focus();
    }

    const clearQuery = () => {
        if (call) call.cancel();

        setPartialQuery('');
        setSuggestions([]);
        focusInput();

        const newQuery = { ...props.query };
        newQuery.misspelling = null;
        newQuery.query = '';
        newQuery.sortType = props.config.sort[0].type;
        newQuery.startRank = 1;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const submitForm = (query?: string) => {
        if (call) call.cancel();
        setSuggestions([]);

        const newQuery = props.query;

        newQuery.misspelling = null;
        newQuery.query = query ? query : partialQuery ? partialQuery : '';
        newQuery.sortType = partialQuery ? '' : props.config.sort[0].type;
        newQuery.startRank = 1;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const clear = partialQuery && (
        <Clear
            clear={() => {
                clearQuery();
            }}
        />
    );

    const submitSuggestion = (suggestion: string) => {
        setShowSuggestions(false);
        setSuggestions([]);
        focusInput();
        submitForm(suggestion);
    };
    const suggestionsList = suggestions && suggestions.length > 0 && (
        <ul
            role="listbox"
            aria-label="Search suggestions"
            aria-activedescendant={activeSuggestionID}
            className={
                showSuggestions
                    ? 'finder__query__suggestions show'
                    : 'finder__query__suggestions hide'
            }
        >
            {[...new Set(suggestions)]
                .slice(0, maximumSuggestions)
                .map((suggestion, i) => (
                    <li key={suggestion} role="option" id={inputId + '--' + i}>
                        <button
                            type="button"
                            onBlur={() => {
                                setActiveSuggestionID('');
                                setShowSuggestions(false);
                            }}
                            onFocus={() => setShowSuggestions(true)}
                            onMouseDown={() => submitSuggestion(suggestion)}
                            onClick={() => submitSuggestion(suggestion)}
                            onKeyDown={(e) => {
                                switch (e.key) {
                                    case escapeKey:
                                        e.currentTarget
                                            .closest('.finder__query')
                                            ?.querySelector('input')
                                            ?.focus();
                                        setSuggestions([]);
                                        setActiveSuggestionID('');
                                        break;
                                    case arrowUp:
                                        e.preventDefault();
                                        e.currentTarget.parentElement
                                            ?.previousElementSibling
                                            ?.querySelector('button')
                                            ?.focus();
                                        break;
                                    case arrowDown:
                                        e.preventDefault();
                                        e.currentTarget.parentElement
                                            ?.nextElementSibling
                                            ?.querySelector('button')
                                            ?.focus();
                                        break;
                                    case homeKey:
                                        e.preventDefault();
                                        const first = e.currentTarget.closest('ul')?.firstChild;
                                        if (first instanceof Element) {
                                            first.querySelector('button')?.focus();
                                        }
                                        break;
                                    case endKey:
                                        e.preventDefault();
                                        const last = e.currentTarget.closest('ul')?.lastChild;
                                        if (last instanceof Element) {
                                            last.querySelector('button')?.focus();
                                        }
                                        break;
                                }
                            }}
                        >
                            {highlightQueryTerm(suggestion, partialQuery)}
                        </button>
                    </li>
                ))}
        </ul>
    );

    // ✅ Server-side fallback
    if (!hasMounted) {
        return (
            <fieldset className="finder__query">
                <div className="finder__query__input">
                    <label className="sr-only" htmlFor={inputId}>
                        {`Search ${props.config.summariseAs && props.config.summariseAs.plural}`}
                    </label>
                    <input
                        type="text"
                        id={inputId}
                        name="query"
                        className="finder__query-input"
                        placeholder={props.config.placeholder}
                        defaultValue={partialQuery}
                    />
                    <button type="submit" className="finder__query__submit">
                        <span
                            className="fal fa-sharp fa-search finder__query__submit__icon"
                            aria-hidden="true"
                        />
                        <span className="finder__query__submit__text">Find</span>
                    </button>
                </div>
            </fieldset>
        );
    }

    // ✅ Interactive version after hydration
    return (
        <fieldset className="finder__query">
            <div className="finder__query__input">
                <div className="finder__query__icon--wrapper">
                    <span
                        className="finder__icon fal fa-sharp fa-search icon"
                        aria-hidden="true"
                    ></span>
                </div>
                <label className="sr-only" htmlFor={inputId}>
                    {`Search ${props.config.summariseAs && props.config.summariseAs.plural}`}
                </label>
                <input
                    autoComplete="off"
                    id={inputId}
                    name="query"
                    placeholder={props.config.placeholder}
                    ref={(input) => {
                        textInput = input;
                    }}
                    type="text"
                    value={partialQuery}
                    onKeyDown={(e) => {
                        if (e.key === escapeKey) clearQuery();
                        else if (e.key === arrowDown && suggestions.length > 0) {
                            e.preventDefault(); 
                            const button = e.currentTarget
                            .parentElement
                            ?.querySelector('.finder__query__suggestions button') as HTMLButtonElement | null; 
                                 
                            button?.focus();
                            setActiveSuggestionID(
                                e.currentTarget.parentElement?.querySelector(
                                    '.finder__query__suggestions li'
                                )?.id || ''
                            );
                        }
                    }}
                    onFocus={() => setShowSuggestions(true)}
                    onBlur={() => setShowSuggestions(false)}
                    onChange={(e) => {
                        const val = e.target.value;
                        setPartialQuery(val);
                        setSuggestions([]);
                        if (call) call.cancel();

                        if (val) {
                            const [promise, newCall] = suggest(
                                props.query.collection,
                                val,
                                props.config.dxp ? props.config.dxp : false
                            ) as [Promise<any>, CancelTokenSource];;
                            setCall(newCall);
                            promise
                                .then((data) => setSuggestions(data))
                                .catch(() => setSuggestions([]));
                        } else {
                            setSuggestions([]);
                        }
                    }}
                />
                {suggestionsList}
                {clear}
                <button
                    type="submit"
                    className="finder__query__submit"
                    onClick={() => submitForm()}
                >
                    <span
                        className="fal fa-sharp fa-search finder__query__submit__icon"
                        aria-hidden="true"
                    />
                    <span className="finder__query__submit__text">Find</span>
                </button>
            </div>
        </fieldset>
    );
}

Finder__Query.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default Finder__Query;
