import React from 'react';
import PropTypes from 'prop-types';
import SelectFilter from './finder__select';
import EventSelectFilter from './finder__eventSelect';
import CheckboxFilter from './finder__checkbox';
import ParamCheckBox from './finder__paramCheckbox';
import EventCheckBox from './finder__eventCheckbox';
import HiddenInput from './finder__hiddenInput';
import TagFilter from './finder__tag';
import Reset from './finder__reset';
import Sort from './finder__sort';
import { gaEvent } from '../../util';
import type { Filters } from '../../types/filters/filters';
import type { FacetDetails } from '../../types/facet';

/**
 * Predicate for whether a facet should be displayed, if it's dependent on
 * another facet being set.
 *
 * @param {object} facet The facet to potentially display.
 * @param {object} facetMap The facets currently set on the query.
 * @return {bool} - Has any dependency been met
 */
function dependencyMet(facet: FacetDetails, facetMap: {[key: string]: string;}) {
    const setFacets = Object.keys(facetMap);

    if (!facet.dependency) {
        return true;
    }

    if (
        setFacets.indexOf(facet.dependency) >= 0 &&
        facetMap[facet.dependency] !== ''
    ) {
        return true;
    }

    return false;
}

/**
 * Component to update facet values in the query.
 *
 * @param {object} props React props.
 * @returns {object} - React component.
 */
function Finder__Filters(props: Filters) {
    const totalMatching =
        props.response &&
        props.response.summary &&
        props.response.summary.fullyMatching;
    const clearFiltersDesktop =
            !props.updating && Object.keys(props.query.facets).length > 0 ? (
                <div className="finder__filters__reset finder__filters__reset--desktop">
                    <Reset
                        clear={props.clear}
                        resetSort={false}
                        matrixState={props.matrixState}
                    />
                </div>
            ) : null,
        clearFiltersMobile =
            (!props.updating && Object.keys(props.query.facets).length > 0) ||
            props.query.sortType !== props.config.sort[0].type ? (
                <Reset
                    clear={props.clear}
                    resetSort={true}
                    matrixState={props.matrixState}
                />
            ) : null;

    const sort =
        !props.matrixState &&
        props.config.sort.length > 1 &&
        props.config.displaySort &&
        totalMatching ? (
            <div className="wrapper--finder__select--sort--mobile">
                <Sort
                    key={props.mobile ? 'sort-mobile' : 'sort-desktop'}
                    config={props.config}
                    query={props.query}
                    update={props.update}
                />
            </div>
        ) : null;

    const submitSSR = props.matrixState ? (
        <div className="finder__filter__submit">
            <button
                type="submit"
                className="finder__query__submit finder__submit"
            >
                <span
                    className="fal fa-sharp fa-search finder__query__submit__icon"
                    aria-hidden="true"
                />{' '}
                <span className="finder__query__submit__text">Find</span>
            </button>
        </div>
    ) : null;

    return (
        <div className="finder__filters">
            <h2 className="finder__filters__heading">
                <span className="finder__filters__heading__text">
                    <span
                        className="far fa-sharp fa-sliders-h icon"
                        aria-hidden="true"
                    />{' '}
                    {`Filter ${props.config.summariseAs && props.config.summariseAs.plural}`}
                </span>
                {clearFiltersMobile}
            </h2>

            <fieldset>
                {!props.hasMounted && props.query.fixedParameters
                    ? props.query.fixedParameters.map((param) => {
                          return (
                              <HiddenInput
                                  key={param.name}
                                  name={param.name}
                                  value={param.value}
                              />
                          );
                      })
                    : null}
                <div className="wrapper--finder_filters--filters">
                    {props.config.facetLabels && props.config.facetLabels.map((facet) => {
                        if (dependencyMet(facet, props.query.facets)) {
                            switch (facet.type) {
                                case 'eventCheckBox':
                                    return (
                                        <EventCheckBox
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            update={props.update}
                                            pastEventsResponse={
                                                props.response.extraSearches &&
                                                props.response.extraSearches
                                                    .past &&
                                                props.response.extraSearches
                                                    .past.response
                                            }
                                            matrixState={props.matrixState}
                                        />
                                    );
                                case 'paramCheckBox':
                                    return (
                                        <ParamCheckBox
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            update={props.update}
                                            pastEventsResponse={
                                                props.response.extraSearches &&
                                                props.response.extraSearches
                                                    .past &&
                                                props.response.extraSearches
                                                    .past.response
                                            }
                                        />
                                    );
                                case 'eventSelect':
                                    return (
                                        <EventSelectFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels && props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                            mobile={props.mobile}
                                        />
                                    );
                                case 'select':
                                    return (
                                        <SelectFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels && props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                            mobile={props.mobile}
                                        />
                                    );
                                case 'checkbox':
                                    return (
                                        <CheckboxFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels && props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                            mobile={props.mobile}
                                        />
                                    );
                                case 'tag':
                                    return (
                                        <TagFilter
                                            key={facet.meta}
                                            facet={facet}
                                            query={props.query}
                                            responseFacet={
                                                props.response &&
                                                props.response.facets
                                                    ? props.response.facets.filter(
                                                          (funnelbackFacet) =>
                                                              funnelbackFacet.name ===
                                                              facet.funnelbackName
                                                      )
                                                    : []
                                            }
                                            update={props.update}
                                            dependencies={props.config.facetLabels && props.config.facetLabels.filter(
                                                (candidate) =>
                                                    candidate.dependency ===
                                                    facet.meta
                                            )}
                                            matrixState={props.matrixState}
                                        />
                                    );
                                default:
                                    gaEvent(
                                        'jsError',
                                        'JavaScript error',
                                        'finder__filters()',
                                        'Unknown filter type in finder__filters.js',
                                        0,
                                        true
                                    );
                            }
                        } else {
                            return null;
                        }
                    })}
                    <p className="finder__filters__nofilters">
                        No filters are valid for the current query.
                    </p>
                    {clearFiltersDesktop}
                    {sort}
                    {submitSSR}
                </div>
            </fieldset>
        </div>
    );
}

Finder__Filters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    response: PropTypes.object,
    update: PropTypes.object,
    clear: PropTypes.func,
    mobile: PropTypes.bool,
    hasMounted: PropTypes.bool,
    matrixState: PropTypes.bool,
    updating: PropTypes.bool,
};

export default Finder__Filters;
