import React from 'react';
import PropTypes from 'prop-types';
import type { Clear, ResetSort } from '../../types/update';

/**
 * Clear input button.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Clear(props: Clear & {resetSort?: ResetSort}) {
    return (
        <button
            className="finder__clear"
            type="button"
            onClick={() => {
                props.clear(props.resetSort);
            }}
        >
            <span className="fa-sharp fa-solid fa-xmark icon " />
            <span className="finder__clear__text">Clear query</span>
        </button>
    );
}

Finder__Clear.propTypes = {
    clear: PropTypes.func,
    resetSort: PropTypes.bool,
};

export default Finder__Clear;
