import { useState, useEffect, useRef } from 'react';
import useHasMounted from './has-mounted';
import replaceHistory from './replace-history';
import { getNonFBParams, getFacetParams } from './url-params';
import { reduceMotion } from '../util';
import { find } from './funnelback';
import scroll from 'zenscroll';

import type { MatrixConfigJSON } from '../types/configJSON';
import type { Response } from '../types/FBresponse';
import type { QueryDetails } from '../types/query';

type Cancelable = { cancel: () => void };


function useLogicWrapper(config: MatrixConfigJSON, results: Response, matrixQuery: {[key: string]: string}, element: HTMLElement | null) {
    const summaryHeadingRef = useRef<HTMLDivElement | null>(null);
    const hasMounted = useHasMounted();
    const screenOffsetRatio = 10;

    const scrollDuration = typeof window !== 'undefined' && !reduceMotion() ? 1000 : 0;

    const initialResults = results || {};
    const nonFBParams = config.facetLabels && config.facetLabels.filter(p => p.nonFBParam);
    const initializeParams = !hasMounted && matrixQuery && config.facetLabels ? getFacetParams(config.facetLabels, matrixQuery, true) : {};


    /**
     * initial state for the Funnelback query, taken from URL parameters and
     * configuration
     **/
    const initialQuery = {
        collection: config.collection,
        facets: initializeParams,
        parameters: matrixQuery && matrixQuery.parameters && matrixQuery.parameters.length > 0 ? matrixQuery.parameters : {},
        fixedFacets: config.fixedFacets,
        fixedParameters: config.fixParameters ? config.fixParameters : [],
        interacted: false,
        misspelling: null as string | null,
        numRanks: config.numRanks,
        query: matrixQuery && matrixQuery.query ? matrixQuery.query : '',
        sortType: config.sort[0].type,
        startRank: 1,
    };

    const [defaultParams, setDefaultParams] = useState(matrixQuery || null);
    const [query, setQuery] = useState<QueryDetails>(initialQuery); // will set once defaultParams is ready
    const [funnelbackResponse, setResponse] = useState<Readonly<Response>>(
            Object.keys(initialResults).length > 0
                ? Object.freeze(results)
                : Object.freeze({
                    bestBets: [],
                    extraSearches: {},
                    facets: [],
                    results: [],
                    spell: null,
                    summary: {
                    currEnd: 0,
                    currStart: 0,
                    numRanks: 0,
                    totalMatching: 0,
                    },
                })
            );


    const [updating, setUpdating] = useState(Object.keys(initialResults).length === 0);
    const [call, setCall] = useState<Cancelable>({ cancel: () => {} });
    const [update, setUpdate] = useState(false);

    // Step 1: Initialize defaultParams after mount
    useEffect(() => {
        if (!defaultParams && typeof window !== 'undefined') {
            const searchParams = new URLSearchParams(window.location.search);
            const obj: { [key: string]: string } = {};
            searchParams.forEach((value, key) => {
                obj[key] = value;
                });
            setDefaultParams(obj);
        }
    }, [defaultParams]);

    // Step 2: Build initial query once defaultParams is ready
    useEffect(() => {
        if (!defaultParams) return;

        const matrixState = !hasMounted && matrixQuery ? true : false;

        const baseQuery = {
            collection: config.collection,
            facets: config.facetLabels && config.facetLabels.length > 0
                ? getFacetParams(config.facetLabels, defaultParams, matrixState)
                : {},
            parameters: config.facetLabels && nonFBParams && nonFBParams.length > 0
                ? getNonFBParams(config.facetLabels, defaultParams, matrixState)
                : {},
            fixedFacets: config.fixedFacets,
            fixedParameters: config.fixParameters || [],
            interacted: false,
            misspelling: null as string | null,
            numRanks: config.numRanks,
            query: !matrixState && defaultParams['query']
                ? defaultParams['query']
                : defaultParams.query || '',
            sortType: !matrixState && defaultParams['query']
                ? ''
                : !matrixState && defaultParams['sort']
                ? defaultParams['sort']
                : config.sort[0].type,
            startRank: !matrixState && defaultParams['start_rank']
                ? parseInt(defaultParams['start_rank'], 10)
                : typeof defaultParams.start_rank === 'string'
                    ? parseInt(defaultParams.start_rank, 10)
                    : 1,
        };

        setQuery(baseQuery);
    }, [defaultParams, hasMounted]);

    // Step 3: Patch query after mount
    useEffect(() => {
        if (hasMounted && defaultParams && defaultParams['num_ranks']) {
            setQuery(prev => ({
                ...prev,
                numRanks: parseInt(defaultParams['num_ranks'], 10) || prev.numRanks,
            }));
        }
    }, [hasMounted, defaultParams]);

    // Step 4: Update response when `update` flips
    useEffect(() => {
        if (!query) return;

        replaceHistory(
            query.query,
            query.startRank,
            query.facets,
            query.parameters || {},
            query.sortType,
            config.facetLabels || [],
            config.sort[0].type,
            hasMounted
        );

        setUpdating(true);

        if (query.interacted && element) {
            const resultsEl = element.querySelector('.finder__results');
            if(resultsEl instanceof HTMLElement){
                scroll.center(
                    resultsEl,
                    scrollDuration,
                    -window.innerHeight / screenOffsetRatio
                );
            }
        }

        call.cancel();

        const [request, requestToken] = find(
            query.collection,
            query.fixedFacets,
            query.fixedParameters,
            query.query,
            query.sortType,
            query.startRank,
            query.numRanks,
            query.facets,
            query.parameters,
            config.dxp || false
        );

        setCall({ cancel: () => requestToken.cancel() });

        request
            .then(data => {
                setResponse(data as Readonly<Response>); // ✅ Freeze before setting
                setUpdating(false);
            })
            .then(() => {
                if (query.interacted && summaryHeadingRef.current) {
                    summaryHeadingRef.current.scrollIntoView({
                        behavior: 'smooth',
                        block: 'nearest',
                        inline: 'center'
                });
                }
            })
            .catch(() => {
                setResponse(initialResults as Readonly<Response>);
                setUpdating(false);
            });
    }, [update, query]);

    const updater = {
        query: (newQuery: QueryDetails) => setQuery(newQuery),
        results: (newUpdate: boolean) => setUpdate(newUpdate),
        updateState: update,
    };

    const clear = (resetSort?:boolean) => {
        const newQuery = {
            ...query,
            sortType: resetSort ? config.sort[0].type : query.sortType,
            facets: {},
            parameters: {},
            startRank: 1,
            misspelling: null as string | null,
            interacted: true,
        };
        call.cancel();
        setQuery(newQuery);
        setUpdate(!update);
    };

    return {
        query,
        funnelbackResponse,
        updating,
        updater,
        clear,
        hasMounted,
        matrixState: !hasMounted && matrixQuery ? true : false,
        summaryHeadingRef,
    };
}

export default useLogicWrapper;
