import React from 'react';
import PropTypes from 'prop-types';
import { uppercaseFirstLetterLowercaseRest } from '../../util';
import type { ResultsCentre } from '../../types/cards/card';
/**
 * Render a Funnelback result as a centre card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Centre(props: ResultsCentre) {
    const school = props.details.listMetadata && props.details.listMetadata.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-sharp fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>
                    {props.details.listMetadata.school.length > 2
                        ? props.details.listMetadata.school
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.listMetadata.school.slice(-1)
                        : props.details.listMetadata.school
                              .join(', and ')}
                </span>
            </p>
        ),
        department = props.details.listMetadata && props.details.listMetadata.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span>{' '}
                <span>
                    {props.details.listMetadata.department.length > 2
                        ? props.details.listMetadata.department
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.listMetadata.department.slice(-1)
                        : props.details.listMetadata.department
                              .join(', and ')}
                </span>
            </p>
        ),
        subject = props.details.listMetadata && props.details.listMetadata.related && (
            <p className="card__type">
                {props.details.listMetadata.related.length > 2
                    ? uppercaseFirstLetterLowercaseRest(
                          props.details.listMetadata.related.join('|')
                      )
                          .split('|')
                          .slice(0, -1)
                          .join(', ') +
                      ' and ' +
                      uppercaseFirstLetterLowercaseRest(
                          props.details.listMetadata.related.join('|')
                      )
                          .split('|')
                          .slice(-1)
                    : uppercaseFirstLetterLowercaseRest(
                          props.details.listMetadata.related.join('|')
                      )
                          .split('|')
                          .join(' and ')}
            </p>
        );
    return (
        <li className="card card--centre">
            <a
                href={props.details.liveUrl}
                className="card__anchor card__details"
            >
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    {subject}
                    <p className="card__description">
                        {props.details.listMetadata && props.details.listMetadata.c && props.details.listMetadata.c[0]}
                    </p>
                    {school}
                    {department}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Centre.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Centre;
