import React from 'react';
import PropTypes from 'prop-types';
import type { EventCheckBox } from '../../types/filters/filters';
import type { Query } from '../../types/query';

function Finder__paramCheckbox(props: EventCheckBox) {
    const dateDayFormat = 10;
    const stringLength = 16,
        stringOffset = -4,
        randomNumber = Math.random().toString(stringLength).slice(stringOffset),
        pastEventsResponseTotal =
            props.pastEventsResponse &&
            props.pastEventsResponse.resultPacket.resultsSummary &&
            props.pastEventsResponse.resultPacket.resultsSummary.fullyMatching,
        pastEventsPartialResponseTotal =
            props.pastEventsResponse &&
            props.pastEventsResponse.resultPacket.resultsSummary &&
            props.pastEventsResponse.resultPacket.resultsSummary
                .partiallyMatching,
        toggleChecked = props.query.parameters && 'lt_eventDate' in props.query.parameters,
        totalText = pastEventsResponseTotal
            ? pastEventsResponseTotal
            : pastEventsPartialResponseTotal
            ? pastEventsPartialResponseTotal
            : 0;

    const currentDate = new Date(),
        currentYear = currentDate.getUTCFullYear(),
        currentDateMonth =
            currentDate.getUTCMonth() >= 10
                ? currentDate.getUTCMonth()
                : '0' + (currentDate.getUTCMonth() + 1),
        currentDateDay = currentDate.getUTCDate(),
        currentDateString = `${currentYear}${currentDateMonth}${
            currentDateDay >= dateDayFormat
                ? currentDateDay
                : '0' + currentDateDay
        }`;

        const toggleFacet = () => {
            const currentDate = new Date();
            const dateDayFormat = 10;
            const currentYear = currentDate.getUTCFullYear();
            const currentDateMonth =
                currentDate.getUTCMonth() + 1 >= 10
                    ? currentDate.getUTCMonth() + 1
                    : '0' + (currentDate.getUTCMonth() + 1);
            const currentDateDay = currentDate.getUTCDate();
            const currentDateString = `${currentYear}${currentDateMonth}${
                currentDateDay >= dateDayFormat ? currentDateDay : '0' + currentDateDay
            }`;
        
            const newQuery: Query['query'] = {
                ...props.query,
                parameters: props.query.parameters ? { ...props.query.parameters } : {},
            };
        
            if (newQuery.parameters && 'lt_eventDate' in newQuery.parameters) {
                // Remove past filters
                delete newQuery.parameters.lt_eventDate;
                delete newQuery.parameters.events;
                newQuery.parameters.ge_eventDate = currentDateString;
                newQuery.sortType = 'adate';
            } else if(newQuery.parameters){
                // Set past filters
                delete newQuery.parameters.ge_eventDate;
                newQuery.parameters.lt_eventDate = currentDateString;
                newQuery.parameters.events = 'past';
                newQuery.sortType = 'date';
            }
        
            newQuery.startRank = 1;
            newQuery.misspelling = null;
            newQuery.interacted = true;
        
            props.update.query(newQuery);
            props.update.results(!props.update.updateState);
        };
        

    if (totalText > 0) {
        if (props.matrixState) {
            return (
                <div className="finder__filter finder__select">
                    <label htmlFor="event--time">Location</label>
                    <select name="event--time" id="event--time">
                        <option value={`ge_eventDate!${currentDateString}`}>
                            Upcoming events
                        </option>
                        <option value={`lt_eventDate!${currentDateString}`}>
                            Past events
                        </option>
                    </select>
                </div>
            );
        } else {
            return (
                <div className="finder__filter finder__checkbox">
                    <input
                        type="checkbox"
                        id={`${props.facet.funnelbackName}--${randomNumber}`}
                        name={props.facet.meta}
                        value={props.facet.checkedValue}
                        onChange={() => toggleFacet()}
                        checked={toggleChecked}
                    />
                    <div
                        className="finder__checkbox__indicator finder__checkbox__indicator"
                        aria-hidden="true"
                        onClick={() => toggleFacet()}
                    >
                        {toggleChecked ? (
                            <span className="fa fa-fw fas fa-check icon"></span>
                        ) : null}
                    </div>
                    <label
                        className="finder__filters__label--always"
                        htmlFor={`${props.facet.funnelbackName}--${randomNumber}`}
                    >
                        {props.facet.name}
                        {!toggleChecked && totalText && ' (' + totalText + ')'}
                    </label>
                </div>
            );
        }
    } else {
        return null;
    }
}

Finder__paramCheckbox.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    pastEventsResponse: PropTypes.object,
    update: PropTypes.object,
    dependencies: PropTypes.arrayOf(PropTypes.object),
    matrixState: PropTypes.bool,
};

export default Finder__paramCheckbox;
